import { connect } from 'react-redux';
import { bindActionCreators, compose } from 'redux';
import { createStructuredSelector } from 'reselect';
import invariant from 'invariant';
import { getNextModal, getLastModal } from './selectors'
import styled from 'styled-components';
import Lorem from 'react-lorem-component';
import Button from '@atlaskit/button';
import CrossIcon from '@atlaskit/icon/glyph/cross';
import Avatar from '@atlaskit/avatar';
import InlineDialog from '@atlaskit/inline-dialog';
import { colors } from '@atlaskit/theme';
import Modal, {
  ModalHeader,
  ModalFooter,
  ModalTitle,
  ModalTransition,
} from '@atlaskit/modal-dialog';

const Hint = styled.span`
align-items: center;
color: ${colors.subtleText};
cursor: help;
display: flex;
`;
const HintText = styled.span`
margin-left: 1em;
`;
const Body = styled.div`
align-items: center;
display: flex;
flex-direction: column;
`;
const Image = styled.img`
max-width: 100%;
margin-bottom: 1em;
margin-top: 1em;
`;

type Props = { 
  onClose: Function, 
  showKeyline: boolean 
};

const Header = ({ onClose, showKeyline }: Props) => (
  <ModalHeader showKeyline={showKeyline}>
    <Button onClick={onClose} appearance="link" spacing="none">
      <CrossIcon label="Close Modal" primaryColor={colors.R400} size="small" />
    </Button>
    </ModalHeader>
    );

type FooterState = { isOpen: boolean };
class Footer extends Component<Props, FooterState> {
  state = { isOpen: false };
  open = () => this.setState({ isOpen: true });
  close = () => this.setState({ isOpen: false });

  render() {
    const { onClose, showKeyline } = this.props;
    const { isOpen } = this.state;

    return (
      <ModalFooter showKeyline={showKeyline}>
        
        </ModalFooter>
        );
  }
}

type dialogProps = { onClose: Function, showKeyline: boolean };


const Dialog = ({ isOpen, modalView, onClose }: dialogProps) => (
  <ModalTransition>
    {isOpen && <Modal
    footer={Footer}
    header={Header}
    onClose={onClose}
    >
    <Body>
      {React.createElement(modalView)}
    </Body>
  </Modal>}
  </ModalTransition>
  );

export class Provider extends Component<Props> {

  static defaultProps = {
    popups: {},
    last: null,
    next: null
  }

  state = {
    lastModal: null,
    nextModal: false,
    isOpen: false
  }

  constructor(props, context = {}) {
    super(props, context);
  }

  static getDerivedStateFromProps(props, state) {

    const { next } = props;

    const { isOpen, lastModal, nextModal } = state;

    let nextStates = {};

    let toNextModal = null;
    let toLastModal = null
    let isAllowed = false;

    if(next) {
      toLastModal = next;
      toNextModal = next;
      isAllowed = true;
    } 

    if(next && lastModal) {
      if(isOpen) {
        isAllowed: false;
      }
    }

    if((lastModal == nextModal) && isOpen) {
      isAllowed = false;
    }

    if(isOpen) {
      isAllowed = false;
    }

    return {
      lastModal: toLastModal,
      nextModal: toNextModal,
      isOpen: isAllowed
    };
  }

  onClose() {
    this.setState({
      nextModal: false,
      lastModal: false
    })
  }

  render() {

    const modalView = this.props.getPopup('signin');

    const { isOpen } = this.state;

    return  (
      <div>
        <Dialog isOpen={isOpen} modalView={modalView} onClose={() => this.onClose()} />
        {Children.only(this.props.children)}
        </div>
        )

  }
}


export default compose(
  connect(
    createStructuredSelector({
      next: getNextModal,
      last: getLastModal
    }),
    (dispatch) => {
      return bindActionCreators({

      }, dispatch);
    }
    ),
  )(Provider);