import React from 'react';
import { bindActionCreators, compose } from 'redux';
import { connect } from 'react-redux';
import { createSelector } from 'reselect';
import { IntlProvider } from 'react-intl';
import { defaultsDeep } from 'lodash';
import { createStructuredSelector } from 'reselect';
import { getLocale } from './selectors';

type Props = {
   children: React.element,
   locale: string,
   messages: object,
}

export class I18nProvider extends React.Component<Props> {

    static defaultProps = {
         messages: {}
    }

    render() {
        const messages = defaultsDeep(this.props.messages[this.props.locale], this.props.messages.en);
        return (
          <IntlProvider locale={this.props.locale} defaultLocale="en" messages={messages}>
              {React.Children.only(this.props.children)}
              </IntlProvider>
              );
    }
}



export default compose(
    connect(
        createStructuredSelector({
            locale: getLocale
        })
        ),
    )(I18nProvider);
