import React, { Component, Suspense } from 'react';
import { connect } from 'react-redux';
import { bindActionCreators, compose } from 'redux';
import { createStructuredSelector } from 'reselect';

import Helmet from 'react-helmet';
import cn from 'classnames';
import { DefaultLayout } from 'components/Layout';
import { FooterWrap } from 'components/Footer';
import { HeaderWrap } from 'components/Header';
import Menu from './Menu';
import Locale from './Locale';
import logo from 'static/img/logo.svg';
import { Nav } from 'components/Navigation';
import getMenu from 'services/Menu/selectors';
import getConfig from 'services/Config/selectors';
import { openModal } from 'services/Popup/actions';

export class App extends React.Component {

  brand = () => {
    const { url } = this.props;
    return (<div className="logo"><a href={url}><img src={logo} width={40} /></a></div>);
  }

  button = () => {
    return (
      <div className="btn-group">
      <a className="btn _outline mr-15" onClick={() => this.props.openModal('signin')} >Sign In</a><a className="btn _outline" onClick={() => this.props.openModal('signup')}>Sign Up</a>
      </div>
      )
  }

  render() {
    return (
      <DefaultLayout className="app">
      <HeaderWrap>

      <Menu name="main" edges={[this.brand()]} shortcuts={[<Locale />, this.button()]} />
      
      </HeaderWrap>
      <div className="app-body">
      <main className="main">
    
      <div className="reactapp">
      {this.props.children}</div>
      </main>
      </div>
      <FooterWrap>
      <React.Fragment>
      <span>
      <a href="https://reactive.io">Reactive</a>
      {' '}
      &copy; 2018 ReactiveLabs.
      </span>
      </React.Fragment>
      </FooterWrap>
      </DefaultLayout>
      );
  }
}

export default compose(
    connect(
        createStructuredSelector({
            config: getConfig
        }),
        (dispatch) => {
            return bindActionCreators({
                openModal
            }, dispatch);
        }
    ),
)(App);