import { fromJS, List, Map } from 'immutable';
import { MODAL_OPEN, MODAL_CLOSE } from './constants';


const initialState = fromJS({
 
});

function popupReducer(state = initialState, action) {
    switch (action.type) {
        case MODAL_OPEN:
        return state.set('opened', action.modal);
        case MODAL_CLOSE:
        return state.set('closed', action.closed);
        default:
        return state;
    }
}

export default popupReducer;
