import { createSelector } from 'reselect';

const getMenus = (state) => {
    return state.get('menu');
};

const getMenu = (state, props) => {
    return props.name;
};

const getMenuItem = createSelector(
    [getMenus, getMenu],
    (menus, menu) => {
    	if(menus.get(menu))
    	{
    		const items = menus.get(menu);
    		return items.toJS();
    	}
        return [];
    },
);

export default getMenuItem;
