import React from 'react';
import Loadable from 'react-loadable';
import { Link, Route, Switch } from 'react-router-dom';

import {
  App,
  Home,
  Page,
  Blog,
  Auth,
  Explore,
  ErrorPage
} from 'containers';

export default (store) => {
  return (
    <App>
    <Switch>
    <Route exact path="/" component={Home} />
    <Route exact path="/explore" component={Explore} />
    <Route exact path="/page/:page" component={Page} />
    <Route exact path="/blog/:category" component={Blog.Category} />
    <Route exact path="/blog/:category/:post" component={Blog.Single} />
    <Route exact path="/auth/login" component={Auth.Login} />
    <Route exact path="/auth/register" component={Auth.Register} />
    <Route exact path="/auth/recovery" component={Auth.Recovery} />
    <Route exact path="/auth/reset" component={Auth.Reset} />
    <Route path="*" component={ ErrorPage.Error400} status={404} />
    <Route path="*" component={ ErrorPage.Error500} status={500} />
    </Switch>
    </App>
    );
};
