import React from 'react';
import PropTypes from 'prop-types';
import hoistNonReactStatics from 'hoist-non-react-statics';
import makeComposer from './makeComposer';

export const composeSaga = ({ key, saga, mode }) => (WrappedComponent) => {

    class InjectSaga extends React.Component {
        
        static WrappedComponent = WrappedComponent;

        static displayName = `withSaga(${(WrappedComponent.displayName || WrappedComponent.name || 'Component')})`;

        static contextTypes = {
            store: PropTypes.object.isRequired,
        };

        composers = makeComposer(this.context.store);

        componentWillMount() {
            const { loadSaga } = this.composers;

            loadSaga(key, { saga, mode }, this.props);
        }

        componentWillUnmount() {
            const { trimSaga } = this.composers;
            trimSaga(key);
        }

        render() {
            return <WrappedComponent {...this.props} />;
        }
    }

    return hoistNonReactStatics(InjectSaga, WrappedComponent);
};

export const composeReducer = ({ key, reducer }) => (WrappedComponent) => {
    class ReducerComposer extends React.Component {
        static WrappedComponent = WrappedComponent;

        static displayName = `withReducer(${(WrappedComponent.displayName || WrappedComponent.name || 'Component')})`;

        static contextTypes = {
            store: PropTypes.object.isRequired,
        };

        composers = makeComposer(this.context.store);

        componentWillMount() {
            const { loadReducer } = this.composers;
            loadReducer(key, reducer);
        }

        render() {
            return <WrappedComponent {...this.props} />;
        }
    }

    return hoistNonReactStatics(ReducerComposer, WrappedComponent);
};
