import React from 'react';
import ReactDOM from 'react-dom';
import ReactDOMServer from 'react-dom/server';
import { renderToString } from 'react-dom/server';
import { Provider } from 'react-redux';
import { StaticRouter as Router } from 'react-router-dom';
import I18nProvider from 'services/Locale/provider';
import ReactiveProvider from 'services/Popup/provider';
import storeFactory, { historyFactory } from 'services/storeFactory';
import routes from './routes';
import popups from './popups';


export default (preploadContext, preloadProps) => {
  const store = storeFactory(preploadContext, preloadProps, true);
  return renderToString(
    <Provider store={store}>
      <I18nProvider>
        <ReactiveProvider getPopup={popups} >
          <Router {...preploadContext}>
            {popups(store)}
          </Router>
        </ReactiveProvider>
      </I18nProvider>
      </Provider>,
      );
};
