import {
    MODAL_OPEN,
    MODAL_CLOSE
} from './constants';

export function openModal(modal) {
    console.log(modal);
    return {
        type: MODAL_OPEN,
        modal: modal
    };
}

export function closeModal(modal) {
    console.log(modal);
    return {
        type: MODAL_CLOSE,
        modal: modal
    };
}
