import { createSelector } from 'reselect';

const getI18n = (state) => {
    console.log(state);
    return state.get('i18n');
}

export const getLocales = createSelector(
    [getI18n],
    (i18n) => {
        if(i18n.get('locales'))
        {
            return i18n.get('locales').toJS();
        }

    });

export const getLocale = createSelector(
    [getI18n],
    (i18n) => {
        return i18n.get('locale');
    });
