import React, { Component, Fragment } from 'react';
import { connect } from 'react-redux';
import { bindActionCreators, compose } from 'redux';
import { createStructuredSelector } from 'reselect';
import { composeSaga, composeReducer } from 'services/composer';
import { FormattedMessage, FormattedHTMLMessage } from 'react-intl';
import classNames from 'classnames';
import { Locales } from 'components/i18n';
import { getLocales, getLocale } from 'services/Locale/selectors';
import { changeLocale } from 'services/Locale/actions';

type Props = {
    locale: string,
    locales: array,
}

export class Locale extends Component<Props> {

    static defaultProps = {

    }

    updateLocale = ({label, value}) => {
        this.props.changeLocale(value);
    }

    render() {
        return (<Locales {...this.props} changeLocale={this.updateLocale} />);
    }
}

export default compose(
    connect(
        createStructuredSelector({
            locales: getLocales,
            locale: getLocale
        }),
        (dispatch) => {
            return bindActionCreators({
                changeLocale
            }, dispatch);
        }
    ),
)(Locale);
