import React, { Component } from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import Select from '@atlaskit/select';

type Props = {
  className: string,
  tag: func | string,
  locale: string,
  locales: array,
  changeLocale: func
}

const Locales = ({
  Tag = 'div', className = 'options', locale = 'en', locales = [], changeLocale = (locale) => {
    console.log('locale trigger')
  }
}) => {

  const classes = classNames(className, 'locale-option');
  const options = [];
  locales.map((value, key) => {
    options.push({label: value.code, value: value.code});
  });
  return (
    <Tag className={classes}>
     <Select
      className="single-select"
      classNamePrefix="react-select"
      options={options}
      placeholder="Locale"
      onChange={changeLocale}
      />
      </Tag>
      );

}


export default Locales;
