import React from 'react';
import ReactDOM from 'react-dom';
import { Provider } from 'react-redux';
import { BrowserRouter as Router } from 'react-router-dom';
import LocaleProvider from 'services/Locale/provider';
import ReactiveProvider from 'services/Popup/provider';
import storeFactory, { historyFactory } from 'services/storeFactory';
import routes from './routes';
import popups from './popups';

export default (dom_id, preploadContext, preloadProps) => {

  const initialState = preloadProps || window.__INITIAL_STATE__;

  const store = storeFactory(preploadContext, preloadProps);

  const domId = dom_id || 'react-view';

  ReactDOM.hydrate(
    <Provider store={store}>
      <LocaleProvider>
        <ReactiveProvider getPopup={popups} >
          <Router {...preploadContext}>
            {routes(store)}
          </Router>
        </ReactiveProvider>
        
      </LocaleProvider>
      </Provider>,
      document.getElementById(domId),
      );
};
