import React, { Component } from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';

type Props = {
	className: string
}

const Nav = ({
	Tag = 'nav', className = 'navbar', items = [], shortcuts = [], edges = []
} : Props) => {

	const classes = classNames(className, 'navbar-expand-lg navbar-light bg-light');

	return (
		<Tag className={classes}>
			{edges.map((value, key) => (value))}
			<button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
				<span className="navbar-toggler-icon" />
			</button>
			<div className="collapse navbar-collapse">
				<ul className="navbar-nav mr-auto">
					{items.map((value, key) => 
						(
							<li className="nav-item" key={key}>
							<a className="nav-link" href={value.path}>
								{value.label}
							</a>
							</li>
							)
						)
					}
				</ul>
				{shortcuts.map((value, key) => (value))}

			</div>
			</Tag>
			);
};

export default Nav;
